import React, { useState } from 'react';
import { Alert, Button } from 'react-bootstrap';
import '../styles/Alert.css'; // Подключение внешнего CSS для анимации

const DataCollectionAlert = () => {
    const [show, setShow] = useState(true);
    const handleClose = () => {
        setShow(false);
        storeDataInLocalStorage();
    };
    const storeDataInLocalStorage = () => {
        localStorage.setItem('dataCollection', '1');
    };
    return (
        <div>
            {show && (
                <Alert
                    variant="info"
                    dismissible
                    onClose={handleClose}
                    className="alert-slide-in myAlert"
                >
                    <Alert.Heading>
                        Уведомление о сборе данных
                    </Alert.Heading>
                    <p>
                        Мы собираем данные для улучшения вашего опыта на сайте и сохраняем их в вашем браузере (localStorage).
                        Это поможет ускорить работу сайта при повторных посещениях. Пожалуйста, продолжайте использовать сайт.
                    </p>
                    <div className="d-flex justify-content-end">
                        <Button onClick={handleClose} variant="outline-secondary">
                            Закрыть
                        </Button>
                    </div>
                </Alert>
            )}
        </div>
    );
};

export default DataCollectionAlert;
